// src/components/BudgetForm/BudgetForm.tsx
"use client";

import { useState } from "react";
import { useBudget } from "../../context/BudgetContext";
import styles from './BudgetForm.module.css';

export default function BudgetForm() {
  const { setTransactions, setBudgetLimit } = useBudget();
  const [type, setType] = useState<"income" | "expense">("income");
  const [category, setCategory] = useState("");
  const [description, setDescription] = useState("");
  const [amount, setAmount] = useState("");
  const [date, setDate] = useState("");
  const [budgetLimit, setBudgetLimitState] = useState(""); 

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    const newTransaction = {
      id: Date.now().toString(),
      type,
      category,
      description,
      amount: parseFloat(amount),
      date,
    };

    // Yeni işlem ekle
    setTransactions((prevTransactions) => {
      const updatedTransactions = [...prevTransactions, newTransaction];
      localStorage.setItem("transactions", JSON.stringify(updatedTransactions)); // LocalStorage'a kaydet
      return updatedTransactions;
    });

    // Formu temizle
    setCategory("");
    setDescription("");
    setAmount("");
    setDate("");
  };

  const handleBudgetLimitChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newLimit = parseFloat(e.target.value);
    setBudgetLimitState(e.target.value);
    setBudgetLimit(newLimit); // Global state'e bütçe limiti ekle
    localStorage.setItem("budgetLimit", e.target.value); // LocalStorage'a kaydet
  };

  return (
    <form onSubmit={handleSubmit} className={styles['form-container']}>
      <div>
        <label>Tip:</label>
        <select value={type} onChange={(e) => setType(e.target.value as "income" | "expense")}>
          <option value="income">Gelir</option>
          <option value="expense">Gider</option>
        </select>
      </div>
      <div>
        <label>Kategori:</label>
        <input
          type="text"
          value={category}
          onChange={(e) => setCategory(e.target.value)}
          required
        />
      </div>
      <div>
        <label>Açıklama:</label>
        <input
          type="text"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          required
        />
      </div>
      <div>
        <label>Tutar:</label>
        <input
          type="number"
          value={amount}
          onChange={(e) => setAmount(e.target.value)}
          required
        />
      </div>
      <div>
        <label>Bütçe Limiti:</label>
        <input
          type="number"
          value={budgetLimit}
          onChange={handleBudgetLimitChange}
          placeholder="Örn: 5000"
        />
      </div>
      
      <div>
        <label>Tarih:</label>
        <input
          type="date"
          value={date}
          onChange={(e) => setDate(e.target.value)}
          required
        />
      </div>
      <button type="submit" className="bg-blue-500 text-white px-4 py-2">
        Ekle
      </button>
    </form>
  );
}
