// contexts/CategoryContext.tsx
"use client";
import React, { createContext, useState, ReactNode, useContext } from 'react';

type CategoryContextType = {
  categories: string[];
  addCategory: (category: string) => void;
};

const defaultState: CategoryContextType = {
  categories: [],
  addCategory: () => {},
};

const CategoryContext = createContext<CategoryContextType>(defaultState);

export const CategoryProvider = ({ children }: { children: ReactNode }) => {
  const [categories, setCategories] = useState<string[]>(JSON.parse(localStorage.getItem('categories') || '[]'));

  const addCategory = (category: string) => {
    const updatedCategories = [...categories, category];
    setCategories(updatedCategories);
    localStorage.setItem('categories', JSON.stringify(updatedCategories));
  };

  return (
    <CategoryContext.Provider value={{ categories, addCategory }}>
      {children}
    </CategoryContext.Provider>
  );
};

export const useCategory = () => useContext(CategoryContext);
