// components/Category/Category.tsx
"use client";
import { useState } from 'react';
import { useCategory } from '../../context/CategoryContext';
import styles from './CategoryForm.module.css'; // CSS modülünü import et

const CategoryForm = () => {
  const [categoryName, setCategoryName] = useState('');
  const { categories, addCategory } = useCategory();

  const handleCategorySubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (categoryName) {
      addCategory(categoryName); // Kategoriyi ekle
      setCategoryName('');
    }
  };

  return (
    <div className={styles.container}>
      <h2 className={styles.title}>Kategori Tanımlama</h2>

      <form onSubmit={handleCategorySubmit} className={styles.form}>
        <input
          type="text"
          placeholder="Kategori Adı"
          value={categoryName}
          onChange={(e) => setCategoryName(e.target.value)}
          className={styles.input}
        />
        <button type="submit" className={styles.button}>
          Kategori Ekle
        </button>
      </form>

      <h3 className={styles.subTitle}>Mevcut Kategoriler:</h3>
      <ul className={styles.list}>
        {categories.length > 0 ? (
          categories.map((category, index) => (
            <li key={index} className={styles.listItem}>{category}</li>
          ))
        ) : (
          <p className={styles.emptyMessage}>Henüz kategori eklenmedi.</p>
        )}
      </ul>
    </div>
  );
};

export default CategoryForm;
